.consciousness-chart {
    background-color: #2c2c2c;
    border: 1px solid #555;
    border-radius: 8px;
    font-family: sans-serif;
    margin: 0; /* Margin is handled by the container */
    display: block; /* Helps with sizing */
    max-width: 100%; /* Make SVG responsive */
    height: auto; /* Maintain aspect ratio */
}

.axis-line {
    stroke: #888;
    stroke-width: 2;
}

.grid-line {
    stroke: #555;
    stroke-dasharray: 2, 2;
}

.axis-label, .legend-label, .ideology-label {
    fill: #ccc;
    font-size: 12px;
}

.ideology-label {
    font-weight: bold;
    font-size: 14px;
}

.line {
    stroke-width: 2;
    /* stroke-dashoffset is controlled by JavaScript */
    opacity: 1; /* Always visible, drawing effect handled by strokeDashoffset */
}

.player-line {
    stroke: #007bff;
}

.npc-line {
    stroke: #dc3545;
}

.point {
    stroke-width: 2;
    stroke: #2c2c2c;
    opacity: 0; /* Points are initially hidden by CSS */
    transition: opacity 0.2s ease-out, transform 0.2s ease-out; /* Smooth fade for points and transform */
    transform-origin: 50% 50%; /* Ensure scaling is from the center */
    transform-box: fill-box; /* Crucial for correct SVG transform-origin */
}

.point.point-pop {
    animation: pointPop 0.3s ease-out forwards; /* Adjust duration and easing */
}

@keyframes pointPop {
    0% { transform: scale(1); }
    50% { transform: scale(2.0); } /* Expand to 2.0 times size */
    100% { transform: scale(1); } /* Return to original size */
}

.player-point {
    fill: #007bff;
}

.npc-point {
    fill: #dc3545;
}

.chart-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px; /* Reduced gap */
    width: 100%;
}

.chart-toggle-buttons {
    display: flex;
    flex-wrap: wrap; /* Allow buttons to wrap on small screens */
    justify-content: center;
    gap: 10px;
}

.chart-toggle-buttons button {
    background-color: #444;
    border: 1px solid #666;
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    margin: 0; /* Remove margin */
    transition: background-color 0.2s;
}

.chart-toggle-buttons button:hover {
    background-color: #555;
}

.chart-toggle-buttons button:disabled {
    background-color: #007bff;
    cursor: not-allowed;
    border-color: #007bff;
}

.tooltip {
    transition: opacity 0.2s;
    pointer-events: none; /* Allows mouse events to pass through */
}

.tooltip-bg {
    fill: rgba(0, 0, 0, 0.8);
    stroke: #aaa;
    stroke-width: 1px;
}

.tooltip-title {
    font-size: 14px;
    font-weight: bold;
    fill: #fff;
}

.tooltip-text {
    font-size: 12px;
    fill: #ddd;
}

.player-text {
    fill: #007bff;
    font-weight: bold;
}

.npc-text {
    fill: #dc3545;
    font-weight: bold;
}


/* Responsive adjustments for smaller screens */
@media (max-width: 599px) {
    .axis-label, .legend-label, .ideology-label {
        font-size: 10px;
    }

    .tooltip-title {
        font-size: 12px;
    }

    .tooltip-text {
        font-size: 10px;
    }
    
    .chart-toggle-buttons button {
        padding: 4px 8px;
        font-size: 0.8em;
    }
}

